    <v-card-text>\r\n                            <h3>Agregar Nuevo </h3>\r\n                            <v-divider></v-divider>\r\n\r\n                            <v-form ref=\"form_ofic\" v-model=\"form_valid\" lazy-validation>\r\n                                <v-row class=\"mt-5\">\r\n                                    <v-col cols=\"12\" sm=\"6\" md=\"2\" class=\"pb-0 \">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            IDUOPER\r\n                                        </h4>\r\n                                        <v-text-field v-model=\"form.iduoper\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field>\r\n                                    </v-col>\r\n                                    <v-col cols=\"12\" sm=\"6\" md=\"5\" class=\"pb-0\">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            Nombre\r\n                                        </h4>\r\n                                        <v-text-field v-model=\"form.nombre\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field>\r\n                                    </v-col>\r\n\r\n                                    <v-col cols=\"12\" sm=\"6\" md=\"5\" class=\"pb-0\">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            Dependencia\r\n                                        </h4>\r\n                                        <v-text-field v-model=\"form.dependencia\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field>\r\n                                    </v-col>\r\n\r\n                                </v-row>\r\n\r\n                                <div class=\"d-flex mt-3\">\r\n                                    <v-spacer></v-spacer>\r\n                                    <v-btn :disabled=\"!form_valid\" color=\"primary\" @click=\"Guardar()\">\r\n                                        Registrar\r\n                                    </v-btn>\r\n                                </div>\r\n                            </v-form>\r\n\r\n                        </v-card-text>\r\n\r\n                    </v-card>\r\n                </v-container>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n<script>\r\nimport Layout from \"@/Layouts/AdminLayout\";\r\nimport SelectOficina from \"@/components/autocomplete/SelectOficina.vue\";\r\nimport axios from \"axios\";\r\nimport { Inertia } from \"@inertiajs/inertia\";\r\n\r\nexport default {\r\n    metaInfo: { title: \"Usuarios\" },\r\n    layout: Layout,\r\n    props: {\r\n        roles: Array,\r\n    },\r\n\r\n    data: () => ({\r\n\r\n        loading_table: false,\r\n        form_valid: null,\r\n\r\n        form: {\r\n            iduoper: null,\r\n            nombre: null,\r\n            dependencia: \"\",\r\n        },\r\n\r\n        obligatorioRules: [(v) => !!v || \"*Obligatorio\"],\r\n        absolute: false,\r\n        drawer: true,\r\n\r\n\r\n    }),\r\n    computed: {\r\n        size_display: {\r\n            get: function () {\r\n                if (this.$vuetify.breakpoint.width > 960) {\r\n                    this.absolute = false;\r\n                    return true;\r\n                }\r\n                this.absolute = true;\r\n                return false;\r\n            },\r\n            set: function (newValue) { },\r\n        },\r\n    },\r\n    methods: {\r\n        async Guardar() {\r\n            if (this.$refs.form_ofic.validate()) {\r\n                let res = await axios.post(\r\n                    \"/admin/oficinas\",\r\n                    this.form\r\n                );\r\n                console.log(res.data);\r\n                if (res.data.estado) {\r\n                    this.$refs.form_ofic.reset();\r\n                }\r\n            }\r\n\r\n            console.log(this.form);\r\n        }\r\n    }\r\n\r\n};\r\n</script>\r\n\r\n<style>\r\n.wrapper-page {\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: #fafafa;\r\n}\r\n\r\n.page-heading {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n\r\n.page-details {\r\n    width: 256px;\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n    border-right: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.page-search {\r\n    width: calc(100% - 256px);\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.content-wrapper {\r\n    position: relative;\r\n    display: flex;\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n.content {\r\n    width: 100%;\r\n    margin-left: 256px;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.content.full {\r\n    width: 100%;\r\n    margin-left: 0;\r\n}\r\n\r\n@media (max-width: 960px) {\r\n    .content {\r\n        margin-left: 0;\r\n    }\r\n\r\n    .page-details {\r\n        width: 200px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 200px);\r\n    }\r\n}\r\n\r\n@media (max-width: 740px) {\r\n    .page-details {\r\n        width: 180px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 180px);\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
